body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
    width: 200px;
    height: 200px;
}
.card-bg {
    background: #ffeb3b4f;
    box-shadow: #FFF 0 -1px 4px, #ff0 0 -2px 10px, #ff8000 0 -10px 20px, red 0 -18px 40px, 5px 5px 15px 5px rgba(0,0,0,0);
    padding: 30px;
    border-radius: 10px;
}

@font-face {
  font-family: 'aaaiight';
  font-style: normal;
  font-weight: normal;
  src: local('aaaiight'), url(/starwarplanets/static/media/aaaiight.4e8add2b.woff) format('woff');
  }
  body {
      background-image: url(/starwarplanets/static/media/astronomy-color-cosmos-41951.3e54e6fe.jpg);
      background-repeat: no-repeat;    
  }
  h1 {
      font-family: 'aaaiight';
      text-shadow: 2px 2px #1b1b19;
      color: white;
  }
